Accounts.emailTemplates.siteName = "UniBooks";
Accounts.emailTemplates.from     = "UniBooks <admin@unibooks.com>";

Accounts.emailTemplates.verifyEmail = {
  subject() {
    return "[UniBooks] Verify Your Email Address";
  },
  text( user, url ) {
    let emailAddress   = user.emails[0].address,
        urlWithoutHash = url.replace( '#/', '' ),
        supportEmail   = "support@unibooks.com",
        emailBody      = `To verify your email address (${emailAddress}) visit the following link:\n\n${urlWithoutHash}\n\n If you did not request this verification, please ignore this email. If you feel something is wrong, please contact our support team: ${supportEmail}.`;

    return emailBody;
  }
}; // Param√©trage de l'email envoy√© (Adresse d'envoi, sujet, objet, contenu texte + URL de v√©rification)

FlowRouter.route( '/verify-email/:token', {
  name: 'verify-email',
  action( params ) {
    Accounts.verifyEmail( params.token, ( error ) =>{
      if ( error ) {
        Bert.alert( error.reason, 'danger' );
      } else {
        FlowRouter.go( '/' );
        Bert.alert( 'Email verified! Thanks!', 'success' );
      }
    });
  }
}); // cr√©ation d'une route Flowrouter pour la v√©rification du lien (v√©rification bool√©enne du login)



